.promo {
  min-height: 520px;
  background-color: #465dff;
  display: flex;
  justify-content: center;
  position: relative;
}

.promo__header {
  margin: 0;
  margin-top: 180px;
  padding: 0;
  max-width: 730px;
  font-size: 50px;
  font-weight: 400;
  line-height: 58px;
  letter-spacing: -0.04em;
  text-align: center;
  color: #fff;
}

.promo__image {
  position: absolute;
  top: 80px;
  width: 320px;
  height: 320px;
}

@media screen and (max-width: 768px) {
  .promo {
    min-height: 780px;
  }
  .promo__header {
    margin-top: 324px;
  }
  .promo__image {
    top: 222px;
  }
}

@media screen and (max-width: 500px) {
  .promo {
    min-height: 565px;
  }
  .promo__header {
    margin-top: 205px;
  }
  .promo__image {
    top: 140px;
    width: 248px;
    height: 248px;
  }
  .promo__header {
    font-size: 31px;
    line-height: 39px;
  }
}
